import React from "react";
import { Link } from "react-router-dom";

const pageLinks = [
    { path: "/calculate-shanten", title: "", description: "" },
    { path: "/efficiency-trainer", title: "", description: "" },
    { path: "/pure-ones-efficiency-trainer", title: "", description: "" },
    { path: "/pure-ones-ready-trainer", title: "", description: "" },
    { path: "/points-calculate-trainer", title: "", description: "" },
    { path: "/fu-calculate-trainer", title: "", description: "" },
];

const MainPage = () => {
    return (
        <div
            style={{
                textAlign: "center",
                padding: "10px",
                maxWidth: "80%",
                margin: "auto", // 
                display: "flex", //  Flexbox
                flexDirection: "column", // 
                alignItems: "center", // 
                justifyContent: "center", // 
            }}
        >
            <div style={{ color: "#007BFF", fontSize: "clamp(28px, 2vw, 32px)", fontWeight: "bold" }}></div>
            <div style={{ display: "grid", gridTemplateColumns: "repeat(auto-fit, minmax(200px, 1fr))", gap: "15px", justifyContent: "center", marginTop: "30px" }}>
                {pageLinks.map(({ path, title, description }) => (
                    <Link
                        key={path}
                        to={path}
                        style={{
                            textDecoration: "none",
                            padding: "15px",
                            borderRadius: "10px",
                            border: "1px solid #007BFF",
                            color: "#007BFF",
                            transition: "background-color 0.3s, color 0.3s",
                            display: "block",
                            backgroundColor: "#f8f9fa",
                            textAlign: "center",
                            fontSize: "24px",
                            fontWeight: "bold",
                            cursor: "pointer",
                            position: "relative", //  hover 
                        }}
                    >
                        <div style={{ fontSize: "clamp(20px, 2vw, 24px)", fontWeight: "bold" }}>{title}</div>
                        <p style={{ fontSize: "calc(0.8 * clamp(20px, 2vw, 24px))", fontWeight: "normal", margin: "10px 0 0" }}>{description}</p>
                        <style>
                            {`
                                a:hover {
                                    background-color: #007BFF !important;
                                    color: #fff !important;
                                }
                            `}
                        </style>
                    </Link>
                ))}
            </div>
        </div>
    );
};

export default MainPage;
